import ajax from '@utils/ajax';

// const serverPre = ajax.serverUrlMock;
const serverPre = ajax.serverUrl;
const serverName = '/operation-task-server';
// const serverNameUser = '/AUTH-USER-CENTER';

// 查询订阅列表
export async function querySubscribeList(params) {
  return ajax.post(
    serverPre(`${serverName}/task-subscribe/querySubscribeList`),
    params
  );
}
// 新增订阅列表
export async function addSubscribeInfo(params) {
  return ajax.post(
    serverPre(`${serverName}/task-subscribe/addSubscribeInfo`),
    params
  );
}
// 编辑订阅列表
export async function editSubscribe(params) {
  return ajax.post(
    serverPre(`${serverName}/task-subscribe/editSubscribe`),
    params
  );
}
// 删除订阅列表
export async function deleteSubscribe(params) {
  return ajax.post(
    serverPre(`${serverName}/task-subscribe/deleteSubscribe`),
    params
  );
}
// 批量删除订阅信息
export async function deleteBatchSubscribe(params) {
  return ajax.post(
    serverPre(`${serverName}/task-subscribe/deleteBatchSubscribe`),
    params
  );
}
// 复制订阅信息
export async function copySubscribe(params) {
  return ajax.post(
    serverPre('${serverName}/task-subscribe/copySubscribe'),
    params
  );
}
// 订阅回显
export async function subView(params) {
  return ajax.post(
    serverPre(`${serverName}/task-subscribe/querySubscribeById`),
    params
  );
}
// 查询所有场景
export async function queryScenes() {
  return ajax.post(serverPre(`${serverName}/task-subscribe/queryScenes`));
}
// 通过场景id查询关联任务信息
export async function getLinkedTaskByScenesId(params) {
  return ajax.post(
    serverPre(`${serverName}/task-subscribe/getLinkedTaskByScenesId`),
    params
  );
}
// 查询岗位
export async function getStation() {
  return ajax.post(
    serverPre(`${serverName}/task-subscribe/station/getAllGroupAndUserDetail`)
  );
}
// 根据岗位id查询用户
export async function getUser(id, status) {
  return ajax.get(
    serverPre(
      `${serverName}/task-subscribe/user/getUserInfoByStationId?id=` +
        id +
        `&status=` +
        status
    )
  );
}
// 任务列表
export async function listTask(params) {
  return ajax.post(serverPre(`${serverName}/task-lib/listTask`), params);
}

