<template>
  <div class="application-about-detail">
    <div class="application-about-detail-item right-item-wrap">
      <base-table-panel
        title="订阅管理"
        class="sortTable noTableBorder"
        :data-source="subscriptionList"
        :right-btns="titleBtns()"
        :loading="subscriptionLoading"
        :columns="columns()"
        :show-summary="false"
        :total="subscriptionTotal"
        has-pagination
        :current-page="seachForm.page"
        :page-size="seachForm.limit"
        :border-style="true"
        :cell-class-name="applicationCellClassName"
        @handleCurrent="handleCurrentChange"
        @selection-change="selectionChange"
        @handleSize="handleSizeChange"
      >
        <template slot="operate-left">
          <el-form
            :inline="true"
            label-suffix=":"
            size="mini"
            @submit.native.prevent
            class="operate-left-form"
          >
            <el-form-item label="岗位">
              <el-select
                clearable
                placeholder="请选择岗位"
                filterable
                v-model="seachForm.station"
                @change="selectStation"
              >
                <el-option
                  v-for="item in stationOptions"
                  :key="item.id"
                  :label="item.station"
                  :value="item.id"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="姓名">
              <el-select
                clearable
                placeholder="请选择姓名"
                filterable
                v-model="seachForm.user"
                @change="selectUser"
              >
                <el-option
                  v-for="item in userOptions"
                  :key="item.id"
                  :label="item.user"
                  :value="item.id"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="场景">
              <el-select
                clearable
                placeholder="请选择订阅场景"
                filterable
                v-model="seachForm.scenes"
                @change="selectScenes"
              >
                <el-option
                  v-for="item in scenesOptions"
                  :key="item.id"
                  :label="item.scenesId"
                  :value="item.id"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="handleSearch">查询</el-button>
            </el-form-item>
          </el-form>
        </template>
      </base-table-panel>
      <el-dialog
        :title="dialogTitle"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose"
      >
        <el-form label-width="80px">
          <el-form-item label="岗位">
            <el-select
              clearable
              placeholder="请选择岗位"
              filterable
              v-model="addForm.station"
              @change="selectCopyStation"
            >
              <el-option
                v-for="item in stationOptions"
                :key="item.id"
                :label="item.station"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="姓名">
            <el-select
              clearable
              placeholder="请选择姓名"
              filterable
              v-model="copyForm.user"
              @change="selectCopyUser"
            >
              <el-option
                v-for="item in userCopyOptions"
                :key="item.id"
                :label="item.user"
                :value="item.user"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="目标岗位">
            <el-select
              clearable
              placeholder="请选择目标岗位"
              filterable
              v-model="copyForm.targetStation"
              @change="selectAddAimsStation"
            >
              <el-option
                v-for="item in stationOptions"
                :key="item.id"
                :label="item.station"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="目标姓名">
            <el-select
              clearable
              placeholder="请选择目标姓名"
              filterable
              multiple
              v-model="copyForm.targetUser"
              @change="selectAddAimsUser"
            >
              <el-option
                v-for="item in userAimsOptions"
                :key="item.id"
                :label="item.user"
                :value="item.user"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="saveCopyBtn()">确 定</el-button>
        </span>
      </el-dialog>
      <el-dialog
        :title="dialogTitle"
        :visible.sync="dialogAddAndEdit"
        width="30%"
        :before-close="handleClose"
      >
        <el-form label-width="80px">
          <el-form-item label="岗位">
            <el-select
              clearable
              placeholder="请选择岗位"
              filterable
              :disabled="disabled"
              v-model="addForm.station"
              @change="selectAddStation"
            >
              <el-option
                v-for="item in stationOptions"
                :key="item.id"
                :label="item.station"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="姓名">
            <el-select
              clearable
              placeholder="请选择姓名"
              filterable
              multiple
              :disabled="disabled"
              v-model="addForm.user"
              @change="selectAddUser"
            >
              <el-option
                v-for="item in userAddOptions"
                :key="item.id"
                :label="item.user"
                :value="item.user"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="订阅场景">
            <el-select
              clearable
              placeholder="请选择订阅场景"
              filterable
              v-model="addForm.scenesId"
              @change="selectAddScenes"
            >
              <el-option
                v-for="item in scenesOptions"
                :key="item.id"
                :label="item.scenesId"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="任务名称">
            <el-input
              v-model="taskName"
              placeholder="请输入关键词回车查询"
              style="width: 212px;"
              @keyup.enter="enterSearch"
            ></el-input>
            <el-radio-group style="float: right;padding: 10px 0;">
              <el-checkbox v-model="checkedTask" @change="checkdTask"
                >仅显示关键任务</el-checkbox
              >
            </el-radio-group>
          </el-form-item>
          <el-form-item label="">
            <el-tree
              :data="tableData"
              show-checkbox
              ref="tree"
              node-key="sceneTask"
              :props="defaultProps"
              :default-checked-keys="defaultChecked"
            >
            </el-tree>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogAddAndEdit = false">取 消</el-button>
          <el-button type="primary" @click="saveBtn()">确 定</el-button>
        </span>
      </el-dialog>
      <el-dialog
        title="场景任务"
        :visible.sync="dialogVisibletask"
        width="60%"
        :before-close="handleClose"
      >
        <div style="text-align: right;padding:0 20px;">
          <el-checkbox v-model="checkedCoreTask" @change="changeCoreTask"
            >仅显示关键任务</el-checkbox
          >
        </div>
        <base-table-panel
          title=""
          class="sortTable noTableBorder tabletask"
          :data-source="subscriptionListTask"
          :loading="subscriptionLoadingTask"
          :columns="columns2()"
          :show-summary="false"
          :total="subscriptionTotalTask"
          has-pagination
          :current-page="seachFormTask.page"
          :page-size="seachFormTask.limit"
          :border-style="true"
          @handleCurrent="handleCurrentChangeTask"
          @handleSize="handleSizeChangeTask"
        >
        </base-table-panel>

        <!-- <span slot="footer" class="dialog-footer">
	      <el-button @click="dialogVisible = false">取 消</el-button>
	      <el-button type="primary" @click="saveCopyBtn()">确 定</el-button>
	    </span> -->
      </el-dialog>
    </div>
  </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex';
import cloneDeep from 'lodash/cloneDeep';
import mixin from '../mixin';
import {
  querySubscribeList,
  addSubscribeInfo,
  editSubscribe,
  deleteSubscribe,
  deleteBatchSubscribe,
  copySubscribe,
  subView,
  queryScenes,
  getLinkedTaskByScenesId,
  getStation,
  getUser,
  listTask
} from '@api/subscriptionManage.js';
import paramsCache from '@mixins/paramCache';

export default {
  name: 'subscriptionManager',
  components: {},
  mixins: [mixin],
  data() {
    return {
      tableData: [],
      taskName: '',
      dialogVisible: false,
      dialogAddAndEdit: false,
      dialogVisibletask: false,
      subscriptionList: [],
      subscriptionListTask: [],
      subscriptionLoading: false,
      subscriptionTotal: 0,
      seachForm: {
        page: 1, // 当前页
        pageSize: 10, // 	分页数量
        scenes: '', // 	场景名称
        station: '', // 	岗位
        user: '', // 	岗位下用户
        userName: '' // 	用户名称
      },
      seachFormTask: {
        isManyOperation: false,
        keyMonitoring: 0,
        limit: 10,
        page: 1,
        scenesId: '',
        stationUser: '',
        tags: [],
        taskName: ''
      },
      subscriptionTotalTask: 0,
      formType: '',
      selectTyle: '',
      addForm: {
        id: '',
        sceneTask: [], //场景下的任务
        scenesId: '', // 场景id
        station: '', // 岗位
        user: [] // 岗位下的用户
      },
      copyForm: {
        id: '',
        station: '', // 岗位
        user: '', // 用户
        targetStation: '', // 目标岗位
        targetUser: [] // 目标姓名
      },
      diolgType: '',
      dialogTitle: '',
      deptCode: '',
      deptOptions: [],
      templateId: '',
      selectionList: [],
      checkTreeUserCode: [],
      defaultChecked: [],
      btnLoading: false,
      stationOptions: [],
      userOptions: [],
      scenesOptions: [],
      userCopyOptions: [],
      userAimsOptions: [],
      stationAddOptions: [],
      checkedTask: true,
      checkedCoreTask: true,
      disabled: false,
      keyTaskStatus: 1,
      defaultProps: {
        children: 'children',
        label: 'sceneTask'
      },
      titleBtns() {
        const _this = this;
        return [
          {
            title: '批量删除',
            code: 'addRole',
            icon: 'icon_delete',
            onClick: _this.deletSelectAll
          },
          {
            title: '复制',
            code: 'addRole',
            icon: 'refresh-line',
            onClick: _this.copyBtn
          },
          {
            title: '新增订阅',
            code: 'addRole',
            icon: 'add-line',
            onClick: _this.subscriptionAdd
          }
        ];
      },
      columns(val) {
        const _this = this;
        return [
          {
            prop: 'id',
            type: 'selection',
            // fixed: true,
            align: 'center',
            width: '30'
          },
          {
            prop: '',
            type: 'index',
            align: 'center',
            label: '序号',
            // fixed: true,
            width: '55'
          },
          {
            prop: 'station',
            label: '岗位',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '200'
          },
          {
            prop: 'user',
            label: '姓名',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '200'
            // render: function(h, { record }) {
            // 	return '1'
            // 	// console.log(JSON.stringify(record),'99999999999999------')
            // }
          },
          {
            prop: 'scenesId',
            label: '订阅场景',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '200'
          },
          {
            prop: '',
            label: '场景任务',
            align: 'center',
            showOverflowTooltip: true,
            'min-width': '60',
            render(h, { record }) {
              return (
                <div>
                  <el-button
                    type="text"
                    on-click={() => _this.taskDetail(record.scenesId)}
                  >
                    任务详情
                  </el-button>
                </div>
              );
            }
          },
          {
            prop: '',
            label: '操作',
            align: 'center',
            width: '120',
            actions: [
              {
                code: 'edit',
                text: '编辑',
                onClick: _this.subscriptionEdit
              },
              {
                code: 'del',
                text: '删除',
                onClick: _this._subscriptionDelet
              }
            ]
          }
        ];
      },
      columns2(val) {
        const _this = this;
        return [
          {
            prop: '',
            type: 'index',
            align: 'center',
            label: '序号',
            // fixed: true,
            width: '55'
          },
          {
            prop: 'taskName',
            label: '任务名称',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '100'
          },
          {
            prop: 'taskDesc',
            label: '任务描述',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '100'
            // render: function(h, { record }) {
            // 	return '1'
            // 	// console.log(JSON.stringify(record),'99999999999999------')
            // }
          },
          {
            prop: 'taskStation',
            label: '责任岗位',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '100'
          },
          {
            prop: 'stationUser',
            label: '责任人',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '100'
            // render: function(h, { record }) {
            // 	return '1'
            // 	// console.log(JSON.stringify(record),'99999999999999------')
            // }
          },
          {
            prop: 'checked',
            label: '任务状态',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '80',
            render(h, { record }) {
              const { checked } = record;
              const text =
                checked === 0 ? '未启动' : checked === 1 ? '启动' : '';
              return h('div', {}, text);
            }
          },
          {
            prop: 'stageId',
            label: '所属阶段',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '100'
            // render: function(h, { record }) {
            // 	return '1'
            // 	// console.log(JSON.stringify(record),'99999999999999------')
            // }
          },
          {
            prop: 'keyMonitoring',
            label: '是否关键监控点任务',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '150',
            render(h, { record }) {
              const { keyMonitoring } = record;
              const text =
                keyMonitoring === 0 ? '否' : keyMonitoring === 1 ? '是' : '';
              return h('div', {}, text);
            }
          },
          {
            prop: 'isMonthlyTarget',
            label: '是否月度目标',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '100',
            render(h, { record }) {
              const { isMonthlyTarget } = record;
              const text =
                isMonthlyTarget === 0
                  ? '否'
                  : isMonthlyTarget === 1
                  ? '是'
                  : '';
              return h('div', {}, text);
            }
          }
        ];
      }
    };
  },
  created() {
    this._getSubscriptionList();
    this._getStation();
    this._queryScenes();
    this._listTask();
    const lett = this;
    document.onkeydown = function(e) {
      const key = window.event.keyCode;
      if (key == 13) {
        lett.enterSearch();
      }
    };
  },
  methods: {
    // 查询按钮
    handleSearch() {
      this._getSubscriptionList();
    },
    //分页
    handleSizeChange() {
      this.seachForm.page = 1;
      this.seachForm.pageSize = 10;
      this._getSubscriptionList();
    },
    // 分页按钮查询
    handleCurrentChange(val) {
      this.seachForm.page = val;
      this._getSubscriptionList();
    },
    //查询订阅列表
    _getSubscriptionList() {
      this.subscriptionLoading = true;
      querySubscribeList(this.seachForm).then((res) => {
        this.subscriptionLoading = false;
        this.subscriptionList = res.data.rows;
        this.subscriptionTotal = res.data.total;
      });
    },
    // 根据岗位id查询用户列表
    selectStation(id) {
      this.selectTyle = '查询';
      this.stationOptions.forEach((item) => {
        if (item.id === id) {
          this.seachForm.station = item.station;
        }
      });
      this._getUser(id, 0);
    },
    //选择用户
    selectUser(id, status) {
      this.userOptions.forEach((item) => {
        if (item.id === id) {
          this.seachForm.user = item.user;
        }
      });
    },
    //选择场景
    selectScenes(id) {
      this.scenesOptions.forEach((item) => {
        if (item.id === id) {
          this.seachForm.scenes = item.scenesId;
        }
      });
    },
    //多选列表
    selectionChange(val) {
      const selectionList = [];
      val.forEach((item) => {
        selectionList.push(item.id);
      });
      this.selectionList = selectionList;
    },
    //批量删除按钮
    deletSelectAll() {
      this.$confirm('确定要删除当前数据吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.btnLoading = true;
        const params = {
          ids: this.selectionList
        };
        this._deleteBatchSubscribe(this.selectionList);
      });
    },
    // 批量删除订阅信息
    _deleteBatchSubscribe(params) {
      deleteBatchSubscribe(params)
        .then(({ error }) => {
          this.btnLoading = false;
          if (!error) {
            this.$notify.success({
              title: '操作',
              message: '批量删除成功'
            });
            this._getSubscriptionList();
          } else {
            this.$notify.error({
              title: '操作',
              message: error.message
            });
          }
        })
        .catch((err) => {
          this.$message.error('批量删除失败！');
          this.btnLoading = false;
        });
    },
    //删除按钮
    _subscriptionDelet(row) {
      this.$confirm('确定要当前数据吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.btnLoading = true;
        const params = {
          id: row.id
        };
        this._deleteSubscribe(params);
      });
    },
    // 删除订阅信息
    _deleteSubscribe(params) {
      deleteSubscribe(params)
        .then(({ error }) => {
          this.btnLoading = false;
          if (!error) {
            this.$notify.success({
              title: '操作',
              message: '删除成功'
            });
            this._getSubscriptionList();
          } else {
            this.$notify.error({
              title: '操作',
              message: error.message
            });
          }
        })
        .catch((err) => {
          this.$message.error('删除失败！');
          this.btnLoading = false;
        });
    },
    //新增按钮
    subscriptionAdd() {
      this.dialogAddAndEdit = true;
      this.disabled = false;
      this.dialogTitle = '新增订阅';
      this.formType = 'add';
      this.addForm = {};
      this.tableData = [];
    },
    //选择复制岗位
    selectCopyStation(id) {
      this.selectTyle = '复制';
      this.stationOptions.forEach((item) => {
        if (item.id === id) {
          this.addForm.station = item.station;
        }
      });
      this._getUser(id, 1);
    },
    //选择新增岗位
    selectAddStation(id) {
      this.selectTyle = '新增';
      this.stationOptions.forEach((item) => {
        if (item.id === id) {
          this.addForm.station = item.station;
        }
      });
      this._getUser(id, 0);
    },
    // 选择新增姓名
    selectAddUser(item) {
      this.addForm.user = item;
    },
    selectCopyUser(item) {
      if (this.selectTyle === '复制') {
        this.copyForm.user = item;
      }
    },
    //选择目标岗位
    selectAddAimsStation(id) {
      this.selectTyle = '目标岗位';
      this.stationOptions.forEach((item) => {
        if (item.id === id) {
          this.copyForm.targetStation = item.station;
        }
      });
      this._getUser(id, 2);
    },
    // 选择目标姓名
    selectAddAimsUser(item) {
      this.copyForm.targetUser = item;
    },
    saveCopyBtn() {
      this._copySubscribe();
    },
    // 新增订阅信息
    _addSubscribeInfo(params) {
      addSubscribeInfo(params)
        .then(({ error }) => {
          this.btnLoading = false;
          this.dialogAddAndEdit = false;
          if (!error) {
            this.$notify.success({
              title: '操作',
              message: '新增订阅成功'
            });
            this._getSubscriptionList();
            this.subscriptionLoading = false;
          } else {
            this.$notify.error({
              title: '操作',
              message: error.message
            });
          }
        })
        .catch((err) => {
          this.$message.error('新增订阅失败！');
          this.btnLoading = false;
        });
    },
    //新增-选择订阅场景
    selectAddScenes(item) {
      this.addForm.scenesId = item;
      const params = {
        keyTaskStatus: this.keyTaskStatus,
        scenesId: this.addForm.scenesId,
        taskName: this.taskName
      };
      this._getLinkedTaskByScenesId(params);
    },
    //enter 任务列表搜索
    enterSearch() {
      const params = {
        keyTaskStatus: this.keyTaskStatus,
        scenesId: this.addForm.scenesId,
        taskName: this.taskName
      };
      this._getLinkedTaskByScenesId(params);
    },
    // 选择任务列表
    selectTask(item) {
      const taskList = [];
      item.forEach((item) => {
        taskList.push(item.sceneTask);
      });
      this.addForm.sceneTask = taskList;
    },
    // 确定新增
    saveBtn() {
      this.addForm.sceneTask = this.$refs.tree
        .getCheckedKeys()
        .concat(this.$refs.tree.getHalfCheckedKeys());
      if (this.dialogTitle === '新增订阅') {
        this.addForm.id = '';
        this._addSubscribeInfo(this.addForm);
      }
      if (this.dialogTitle === '编辑订阅') {
        this.addForm.user = this.addForm.user[0];
        this._editSubscribe(this.addForm);
      }
    },
    // 单选是否是关键任务
    checkdTask(item) {
      if (item === false) {
        this.keyTaskStatus = 0;
      } else {
        this.keyTaskStatus = 1;
      }
    },
    // 编辑按钮
    subscriptionEdit(row) {
      this.addForm = {};
      this.dialogAddAndEdit = true;
      this.disabled = true;
      this.dialogTitle = '编辑订阅';
      const params = {
        id: row.id
      };
      this.addForm.id = row.id;
      this._subView(params);
    },
    // 编辑订阅信息
    _editSubscribe(params) {
      editSubscribe(params)
        .then(({ error }) => {
          this.btnLoading = false;
          this.dialogAddAndEdit = false;
          if (!error) {
            this.$notify.success({
              title: '操作',
              message: '编辑订阅成功'
            });
            this._getSubscriptionList();
            this.addForm = {};
          } else {
            this.$notify.error({
              title: '操作',
              message: error.message
            });
          }
        })
        .catch((err) => {
          this.$message.error('编辑订阅失败！');
          this.btnLoading = false;
        });
    },
    // 复制订阅信息
    _copySubscribe() {
      this.copyForm.id = '';
      copySubscribe(this.copyForm)
        .then(({ error }) => {
          this.btnLoading = false;
          this.dialogVisible = false;
          if (!error) {
            this.$notify.success({
              title: '操作',
              message: '复制订阅成功'
            });
            this._getSubscriptionList();
          } else {
            this.$notify.error({
              title: '操作',
              message: error.message
            });
          }
        })
        .catch((err) => {
          this.$message.error('复制订阅失败！');
          this.btnLoading = false;
        });
    },
    // 订阅信息回显
    _subView(params) {
      subView(params).then((res) => {
        const row = res.data;
        const userArray = [];
        this.addForm = row;
        userArray.push(row.user);
        this.addForm.user = userArray;
        let scenesId = '';
        this.scenesOptions.forEach((item) => {
          if (item.scenesId === row.scenesId) {
            scenesId = item.id;
          }
        });
        this.addForm.scenesId = scenesId;
        if (row.keyStatus === 1) {
          this.checkedTask = true;
        } else {
          this.checkedTask = false;
        }
        const params = {
          keyTaskStatus: row.keyStatus,
          scenesId: scenesId,
          taskName: this.taskName
        };
        this._getLinkedTaskByScenesId(params);
        this.defaultChecked = row.sceneTask;
      });
    },
    // 查询所有场景信息
    _queryScenes() {
      queryScenes().then((res) => {
        this.scenesOptions = res.data;
      });
    },
    // 通过场景id 获取任务信息
    _getLinkedTaskByScenesId(params) {
      getLinkedTaskByScenesId(params).then((res) => {
        this.tableData = res.data;
      });
    },
    //获取岗位列表
    _getStation() {
      getStation().then((res) => {
        this.stationOptions = res.data;
      });
    },
    // 根据岗位id查询用户
    _getUser(id, status) {
      getUser(id, status).then((res) => {
        if (this.selectTyle === '查询') {
          this.userOptions = res.data;
        }
        if (this.selectTyle === '复制' || this.selectTyle === '新增') {
          this.userCopyOptions = res.data;
          this.userAddOptions = res.data;
        }
        if (this.selectTyle === '目标岗位') {
          this.userAimsOptions = res.data;
        }
      });
    },
    // 复制按钮
    copyBtn() {
      this.dialogVisible = true;
      this.dialogTitle = '复制订阅';
      this.formType = 'copy';
      this.selectTyle = '复制';
      this.copyForm = {};
      this.addForm = {};
    },
    // 任务详情
    taskDetail(val) {
      this.dialogVisibletask = true;
      let scenesId = '';
      this.scenesOptions.forEach((item) => {
        if (item.scenesId === val) {
          scenesId = item.id;
        }
      });
      this.seachFormTask.scenesId = scenesId;
      this._listTask(this.seachFormTask);
    },
    handleCurrentChangeTask(val) {
      this.seachFormTask.page = val;
      this._listTask();
    },
    handleSizeChangeTask() {
      this.seachFormTask.page = 1;
      this.seachFormTask.limit = 10;
      this._listTask();
    },
    _listTask() {
      listTask(this.seachFormTask).then((res) => {
        this.subscriptionListTask = res.data.rows;
        this.subscriptionTotalTask = res.data.total;
      });
    },
    changeCoreTask(item) {
      if (item === false) {
        this.seachFormTask.keyMonitoring = 1;
      } else {
        this.seachFormTask.keyMonitoring = 0;
      }
      this._listTask();
    }
  }
};
</script>
